<template>
  <div>
    <v-layout style="background:#fff" class="py-5 px-3" align-center justify-center wrap column>
        <v-flex xs12 md12>
          <v-layout wrap>
            <v-flex xs12 md1></v-flex>
            <v-flex xs12 md7 pr-5>
              <p class="heading">Events</p>
              <p
                class="text"
              >Whatever you do, do it well. Do it so well that when people see you do it they will want to come back and see you do it again and they will want to bring others and show them how well you do what you do.</p>
            </v-flex>
            <v-flex xs12 md4 hidden-sm-and-down>
              <v-img :src="event" height="auto" width="50%"></v-img>
            </v-flex>
          </v-layout>
        </v-flex>
      </v-layout>
    <v-container class="border pa-4">
      <v-layout wrap v-if="loading">
        <v-flex md4 xs12 v-for="i in 3" :key="i" my-2>
          <v-skeleton-loader
            class="mx-auto"
            max-width="300"
            type="card"
          ></v-skeleton-loader>
        </v-flex>
      </v-layout>

      <v-layout wrap v-else>
        <v-flex xs12 md3 v-for="(event, i) in events" :key="i" px-2 py-2>
            <v-img :src="event" style="border: 1px solid #989A9C;">
                <template v-slot:placeholder>
                <v-layout align-center justify-center fill-height>
                  <v-progress-circular indeterminate color="#F60044"></v-progress-circular>
                </v-layout>
              </template>
            </v-img>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
import axios from "axios";
import event from "@/assets/logos/events.svg";
import pic from "@/assets/1.jpeg";
export default {
  data() {
    return {
      loading: true,
      events: [],
      event: event,
      pic: pic
    };
  },
  mounted() {
    this.loadall();
    document.title = "Rotract | Events";
  },
  methods: {
    loadall() {
      let apiurl = "https://rotract-club-of-coimbatire.github.io/api-calls/event.json";
      axios.get(apiurl).then((res) => {
        if (res.data.event) {
          this.loading = false;
          res.data.event.forEach((e) => {
            this.events.push(e);
          });
        }
      });
    },
  },
};
</script>
